import axios from "axios"
const url= "/api/notes"

const getAll= ()=>{
   const request =axios.get(url)
   return request.then(response=>response.data)
}
const postAll=(newObj)=>{
    const request= axios.post(url,newObj)
    return request.then(response=>response.data)
}
const putAll=(id,newObj)=>{
    const request = axios.put(`${url}/${id}`,newObj)
    return request.then(response=>response.data)
}
const remove =(id,newObj)=>{
    const request = axios.delete(`${url}/${id}`,newObj)
    return request.then(response=>response.data)
}
const serviceData={getAll,postAll,putAll,remove};
export default serviceData;