import React from "react"
const Note=({notez,toggleImp,deleteHandler})=>{
    const label=notez.important ? "make not important" :"make important";
    return(
        <div>
            <li>
            {notez.content} <button  className= {notez.important ? "not_imp" :"imp_btn"}onClick={toggleImp}>{label}</button> <button className= "delBtn" onClick={deleteHandler}>
            delete
          </button>
            </li>
            
        </div>
        
    )

}
export default Note