import React from "react"
const Notification=({msgsucc,msgerr})=>{
    if (msgsucc === null) {
        return null
      }
    else if (msgerr === null) {
        return null
    }
    
      return (<>
        <div className="error">
          {msgerr}
        </div>
         <div className="success">
         {msgsucc}
         
       </div>
       </>)
    
}

export default Notification;



