import React, { useState ,useEffect} from "react";
import Notification from "./Notification"
import Services from "./Services";
import logo from "./logo.png"
import Note from "./Note"
const App=()=>{
  const [notes,setNotes]=useState([])
  const [newNote,setNewnote]=useState("")
  const [showAll,setShowall]=useState(false)
  const [errMsg,setErrmasg]=useState("")
  const [successMsg,setSuccessmsg]=useState("")
  useEffect(()=>{
  Services.getAll()
  .then(initialNotes=>{
    setNotes(initialNotes)
  })
  },[])

const addNote=(event)=>{
  event.preventDefault()
  const newObject={
    id:notes.length+1,
    content:newNote,
    date: new Date(),
    important:Math.random()<0.5
  }
  Services.postAll(newObject).then(response=>{
    setNotes(notes.concat(response))
    setNewnote("")
    setSuccessmsg(
      `Note '${newObject.content}' is addded`
    )
    setTimeout(() => {
      setSuccessmsg(null)
    }, 3000)
  })
  
}
const notesToshow= showAll ? notes:
  notes.filter((val)=>val.important===true)

  const toggleImp=(id)=>{
    console.log(`toggle the importance of ${id}`)
    
    const note=notes.find(n=>n.id===id)
    console.log(note)
    const changeNote={...note,important:!note.important}
    
    Services.putAll(id,changeNote).then(response=>{
      setNotes(notes.map(val=>val.id!==id ? val : response))

      setSuccessmsg(
        `Note '${note.content}' is updated`
      )
      setTimeout(() => {
        setSuccessmsg(null)
      }, 3000)

    }).catch(error => {
      setErrmasg(
        `Warning!! '${note.content}' was already removed from server`
      )
      setTimeout(() => {
        setErrmasg(null)
      }, 3000)
      setNotes(notes.filter(n => n.id !== id))
    })
  }
  const deleteHandler=(id,content)=>{
    if(window.confirm(`Delete ${content} ?`)){
      console.log(`Deleted the note of id ${id}`);
      const note = notes.filter((n)=>n.id!==id);
      console.log(note);

      Services.remove(id).then(response=>{
        setNotes(notes.filter((val)=>val.id !==id ? val:response))
        setSuccessmsg(
          `Note '${content}' is deleted`
        )
        setTimeout(() => {
          setSuccessmsg(null)
        }, 3000)
    
      }).catch(error => {
        setErrmasg(
          `Warning!! '${notes.content}' was already removed from server`
        )
        setTimeout(() => {
          setErrmasg(null)
        }, 3000)
      })
    
    }
  }
return(<>
<div className="container">
<div className="logo">
<a href ="#"><img src={logo} alt="logo"/></a>
</div>
<h1 >Notes</h1>
</div>
<Notification msgerr={errMsg} msgsucc={successMsg}/>

<div>
  <button  className={"all_btn"} onClick={()=>setShowall(!showAll)}>show {showAll ? "important":"all"}</button>
</div>
<div className="sec_part">
<ul >
   {
     notesToshow.map(val=>
      <Note key={val.id} notez={val}  deleteHandler={()=>deleteHandler(val.id,val.content)} toggleImp={()=>toggleImp(val.id)}/>)
   }
</ul>
<form onSubmit={addNote}>
  <input className="input" onChange={(event)=>setNewnote(event.target.value)}  placeholder="new note.." value={newNote}/>
  <button className ="save"type="submit" >save</button>
</form>
</div>




</>)

}
export default App